.mbl-sect {
    padding-top: 130px;
    padding-left: 25px;
    padding-right: 25px;
}

.mbl-head {
    font-size: 50px;
    color: black;
}

.filter-mbl {
    font-size: 26px;
    font-weight: 400;
    line-height: 1.2;
}

#Sort {
    border: none;
    cursor: pointer;
    /* padding-left: 20px; */
}

#Sort:focus {
    outline: none;
}

.filter-category {
    font-size: 16px;
    position: relative;
}

.filter-sep {
    position: relative !important;
}

.filter-sign {
    cursor: pointer;
}

.underline {
    position: absolute !important;
    top: 57px;
    left: 0;
    width: 191px;
    height: 1px;
    background: var(--bs-fourth);
    border-radius: 9px;
}

.prd-cards {
    width: 33%;
}

.mbl-map {
    cursor: pointer;
}

.mbl-img {
    transition: all .3s ease-in-out;
}

.mbl-img:hover {
    transform: scale(1.2);
}

.mbl-main {
    position: relative;
    /* z-index: 5; */
}

.Firstcat-menu {
    cursor: pointer;
}

