    }

    .mbl-saletag {
        width: 35%;
        font-size: 12px;
        border-radius: 20px;
        top: 5px;
        left: 1px;
    }

    .custom-select-container {
        position: relative;
        display: inline-block;
    }

    .custom-select {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: transparent;
        padding-right: 25px;

    }

    .custom-select-icon {
        position: absolute;
        top: 50%;
        right: 8px;
        transform: translateY(-50%);
        color: blue;
        font-size: 20px;
    }

    #Sort {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        background-color: transparent;
        padding-right: 0;
        width: 100%;
        height: 12vh !important;
        padding: 0px;
        position: absolute !important;
        z-index: 1 !important;
    }
}